.App {
  text-align: left;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.App-list {
  list-style: none;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
  }
	to {
	transform: rotate(360deg);
  }
}


/*** Start here ***/

/** External Resources **/

@import url('https://fonts.googleapis.com/css?family=Montserrat|Roboto');


/** Variables **/

$color-primary-accent: #2980b9;
$color-primary-text: #ffffff;
$color-secondary-accent: #ffffff;
$color-secondary-text: #000000;

$navbar-padding: 0.5;



body {
	font-family: 'Montserrat', sans-serif;
}

.landing {
	background: $color-primary-accent;
	color: $color-primary-text;
	display: flex;
	flex-direction: column;
	justify-content: center;

	height: stretch;
	width: stretch;
	box-sizing: border-box;
}

.landing-wrapper {
	display: flex;
	flex-direction: column;
	justify-content: center;
	flex-grow: 1;

	margin: 0 5em 5em 5em;

	.header {

		h1 {
			font-size: 100px;
		}

		h3 {
			font-size: 20px;
		}
	}

	.subheader {
		ul {
			list-style: none;

			margin: 0;
			padding: 0;

			font-size: 16px;

			li {
				display: inline-block;
				margin-right: 0.5em;
				padding: 0.5em 0 0.5em 0;

				a {
					padding: 1em;
					color: $color-primary-text;
					text-decoration: none;
				}

				.fa-icon {
					font-size: 30px;
				}
			}
		}
	}
}

.navbar {
	width: stretch;
	height: auto;
	flex-basis: auto;
	background: none;

	margin-right: 2em;

	ul {
		list-style: none;
		text-align: right;

		margin: 0;
		padding: $navbar-padding + em;

	}
}

.navbar-elem {
	display: inline-block;

	padding: $navbar-padding + em;

	a {
		position: relative;
		padding: ($navbar-padding / 2) + em;

		color: $color-primary-text;
		font-size: 20px;
		text-decoration: none;
	}
	a:after {
		content: "";
		position: absolute;
		right: 0;
		width: 0;
		bottom: -3px;
		background: $color-primary-text;
		height: 2px;
		transition-property: width;
		transition-duration: 0.3s;
		transition-timing-function: ease-out;
	}
	a:hover:after,
	a:focus:after,
	a:active:after {
		left: 0;
		right: auto;
		width: 100%;
	}
	.active:after {
		left: 0;
		right: auto;
		width: 100%;
	}
}
