body {
  font-family: 'Montserrat', sans-serif; }

.landing {
  background: #2980b9;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: -webkit-fill-available;
  height: -moz-available;
  height: stretch;
  width: -webkit-fill-available;
  width: -moz-available;
  width: stretch;
  box-sizing: border-box; }

.landing-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-grow: 1;
  margin: 0 5em 5em 5em; }
  .landing-wrapper .header h1 {
    font-size: 100px; }
  .landing-wrapper .header h3 {
    font-size: 20px; }
  .landing-wrapper .subheader ul {
    list-style: none;
    margin: 0;
    padding: 0;
    font-size: 16px; }
    .landing-wrapper .subheader ul li {
      display: inline-block;
      margin-right: 0.5em;
      padding: 0.5em 0 0.5em 0; }
      .landing-wrapper .subheader ul li a {
        padding: 1em;
        color: #ffffff;
        text-decoration: none; }
      .landing-wrapper .subheader ul li .fa-icon {
        font-size: 30px; }

.navbar {
  width: -webkit-fill-available;
  width: -moz-available;
  width: stretch;
  height: auto;
  flex-basis: auto;
  background: none;
  margin-right: 2em; }
  .navbar ul {
    list-style: none;
    text-align: right;
    margin: 0;
    padding: 0.5em; }

.navbar-elem {
  display: inline-block;
  padding: 0.5em; }
  .navbar-elem a {
    position: relative;
    padding: 0.25em;
    color: #ffffff;
    font-size: 20px;
    text-decoration: none; }
  .navbar-elem a:after {
    content: "";
    position: absolute;
    right: 0;
    width: 0;
    bottom: -3px;
    background: #ffffff;
    height: 2px;
    -webkit-transition-property: width;
    transition-property: width;
    -webkit-transition-duration: 0.3s;
            transition-duration: 0.3s;
    -webkit-transition-timing-function: ease-out;
            transition-timing-function: ease-out; }
  .navbar-elem a:hover:after,
  .navbar-elem a:focus:after,
  .navbar-elem a:active:after {
    left: 0;
    right: auto;
    width: 100%; }
  .navbar-elem .active:after {
    left: 0;
    right: auto;
    width: 100%; }

